body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.topBarContainer {
 background-color: black;
 display: flex;
 flex-direction: row;
 justify-content: center;
 flex-wrap: wrap;
 width: 100%;
 height: 200px;
 position: sticky;
 top:0;
}

.mainBarContainer {
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  height: 1200px;
 }

 .image {
   flex: 1;
 }
 
.imgFlex{
  flex: 1;
  height:100%;
}

.announcementFlex{
  flex: 1;
  align-content: center;
  margin: auto;
  height: 100%;
  width: 200px;
}

.loginFlex {
  flex: 0.7;
  background-color: black;
  height:100%;
  width: 200px;
 }
 
 .loginBox {
  background-color: grey;
  height: 90%;
  margin-top: 3%;
  margin-right: 3%;
 }

 .mainbar {
   background-color: black;
   height: auto;
 }

 .posts {
   background-color: #2e2e2e;
   margin: auto;
   width: 75%;
   height: 100%;
 }

 .onlyTextPost {
  background-color: white;
  margin: auto;
  width: 90%;
  height: 20%;
 }

.postFlex {
  flex: 1;
  background-color: black;
  width: 100%;
  padding: 1em;
 }

.postFlex > div{
  background-color: grey;
  width: 100%;
  height: 100%;
  text-align: center;
}